import React from 'react';
import './../../../scss/headerSection.scss'
import {Link} from "react-router-dom";
import ph_first from "./laser_epilation1.avif"
import ph_second from "./back_vertical.avif"
import ph_third from "./masked_face.avif"
import phj_first from "./laser_epilation1.jpg"
import phj_second from "./back_vertical.jpg"
import phj_third from "./masked_face.jpg"


function HeaderSection(props) {
    return (
        <div className="headerSection">
            <div className="headerSection__wrapper">

                <div className="headerSection__box">
                    <img src={(/iPhone|iPad|iPod|BlackBerry|BB|IEMobile|Windows Phone/i.test(navigator.userAgent)) ? phj_third : ph_third} alt="Услуги косметологии"/>
                    <div className="headerSection__box__content">
                        <h2 className="title__face">Лицо</h2>
                        <Link to="/face" >
                            <button className="to__pricelist">Услуги</button>
                        </Link>
                    </div>
                </div>

                <div className="headerSection__box">
                    <img src={(/iPhone|iPad|iPod|BlackBerry|BB|IEMobile|Windows Phone/i.test(navigator.userAgent)) ? phj_second : ph_second} alt="Услуги массажа"/>
                    <div className="headerSection__box__content">
                        <h2 className="title__massage">Тело</h2>
                        <Link to="/massage" className="headerSection__box__href">
                            <button className="to__pricelist">Услуги</button>
                        </Link>
                    </div>
                </div>

                <div className="headerSection__box">
                    <img src={(/iPhone|iPad|iPod|BlackBerry|BB|IEMobile|Windows Phone/i.test(navigator.userAgent)) ? phj_first : ph_first} alt="Услуги эпиляции"/>
                    <div className="headerSection__box__content">
                        <h2 className="title__cream">Эпиляция</h2>
                        <Link to="/device-cosmetology" className="headerSection__box__href">
                            <button className="to__pricelist">Услуги</button>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HeaderSection;